.call_context {
	position: relative;
	z-index: 70;
	flex-shrink: 0;
	display: flex;
	flex-direction: column;
	gap: 10px;
	cursor: pointer;
}

.phone {
	font-size: 18px;
	transition: color var(--transition);
}

.call_context:hover .phone{
	color:var(--red);
}


.order_text {
	font-size: 14px;
	color: var(--red);
}

@media (min-width: 1600px){
	.call_context {
		flex-direction: row;
		align-items: center;
		gap: 30px;
		line-height: 26px;
		}
}

@media (max-width: 1360px){
	.call_context.blocked {
		display: none;
		}
}


