.main_menu_list{
	position: relative;
	z-index: 70;
	grid-area: menu_list;
	max-width: 450px;
	width: 100%;
	/* height: 88px; */
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: 25px;
}

.main_menu_list.content{
	font-size: 22px;
	display: flex;
	flex-direction: column;
	gap:40px;

}


.main_menu_item {
	flex-shrink: 0;
  cursor: pointer;
  transition: all .3s;
  text-decoration: none;
  color: var(--white);
}

.main_menu_item:hover {
  color: var(--orange);
}

@media (max-width: 1160px){
	.main_menu_list{
		display: none;
	}

}